import Modal from '../UI/Modal'
import classes from './Order.module.css'
import * as emailjs from 'emailjs-com'


const Cart = (props) =>{

    const tutupHandler = () =>{
        props.backOrderHandler()
    }

    const orderSubmitHandler = (e) =>{
        e.preventDefault()
        emailjs.sendForm('service_q1frghd', 'larisplastik',e.target, 'user_gn3c5efr6R02GiJ29cs8G')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        e.target.reset()
        props.closeOrderHandler()
    }

    const dataBarang = 
    <ul className={classes['cart-items']}>{props.belanjaan.map((item,index) => 
            <li>
                <input name='pesanan_barang' value={item.nama} />
                <input name='pesanan_jumlah' value={props.jumlahBarang[index]} />
            </li>)}
    </ul>
    
    return( <Modal>
            <form onSubmit={orderSubmitHandler} className={classes.data}>
                <div className={classes.dataForm}>
                    <span><label>Nama</label></span>
                    <span><input name='nama' type='text'/></span>
                    
                </div>
                
                <br/>
                <div className={classes.dataForm}>
                    <label>Alamat</label>
                    <input name="alamat" type='text'/>
                </div>
                <div className={classes.barang}>
                    <span>Nama Barang</span>
                    {dataBarang}
                </div>
                <div className={classes.total}>
                    <span>Jumlah</span>
                    <input name='totalharga' value= {props.totalHarga}/>
                </div>
                <div className={classes.actions}>
                    <button onClick={tutupHandler} className={classes['button--alt']}>Kembali</button>
                    <button type='submit' className={classes.button}>Order</button>
                </div>
            </form>
            
        </Modal>
    )
}

export default Cart