import Modal from '../UI/Modal'
import classes from './Cart.module.css'


const Cart = (props) =>{
    


    const tutupHandler = () =>{
        props.tutupHandler(0)
    }

    const orderHandler = () =>{
        props.orderHandler()

    }

    const CartItem = 
    <ul className={classes['cart-items']}>{props.belanjaan.map((item,index) => 
            <li>
                <p>{item.nama}</p>
                <p>{item.harga}</p>
                <p>{props.jumlahBarang[index]}</p>
            </li>)}
    </ul>
    return( <Modal>
            {CartItem}
            <div className={classes.total}>
                <span>Jumlah</span>
                <span>{props.totalHarga}</span>
            </div>
            <div className={classes.actions}>
                <button onClick={tutupHandler} className={classes['button--alt']}>Tutup</button>
                <button onClick={orderHandler} className={classes.button}>Order</button>
            </div>
        </Modal>
    )
}

export default Cart