import React from "react"
import KatalogForm from './KatalogForm'
import classes from './Katalog.module.css'

const Katalog = (props) =>{
    const barangHandler = (data) => {
        props.barangHandler(data)
    }
    return <React.Fragment>
        <li className={classes.KatalogItem}>
            <img src={props.img} />
            <div className={classes.Desc}>
                <p>
                    <span><h2>{props.nama}</h2></span>
                    <span><h4>{props.harga}</h4></span>
                </p>
                
            </div>
            <KatalogForm id={props.id} barangHandler={barangHandler}/>
            <br/>
        </li>
    </React.Fragment>
}

export default Katalog