import Modal from '../UI/Modal'
import classes from './LoginForm.module.css'


const LoginForm = (props) =>{
    const daftarHandler = (e) =>{
        e.preventDefault()
        props.daftarHandler(1)
    }
    const loginUser = () =>{
        console.log('Login')
    }
    const loginTutupHandler = () =>{
        props.loginTutupHandler(0)
    }

    return( <Modal>
            <form onSubmit={loginUser} className={classes.actions}>
                <label>Username</label>
                <input placeholder='Masukkan UserName Anda'/>
                <br/>
                <button onClick={loginTutupHandler} className={classes['button--alt']}>Tutup</button>
                <button type='submit' className={classes.button}>Login</button>
                <p>Belum memiliki akun? <button className={classes.btnDaftar} onClick={daftarHandler}>Klik Disini</button></p>
            </form>

        </Modal>
    )
}

export default LoginForm