import React,{useState,useEffect} from 'react';
import Header from './Components/Layout/Header'
import Body from './Components/Layout/Body';
import kresekImage from './Assets/kresek1.jpg'
import plastikImage from './Assets/plastik.jpg'
import sedotanImage from './Assets/sedotan.jpg'
import bumbuImage from './Assets/Bumbu.jpg'
import sterofoamImage from './Assets/Sterofoam.jpg'
import Cart from './Components/Cart/Cart';
import Order from './Components/Order/Order'
import swal from 'sweetalert';
import LoginForm from './Components/Login/LoginForm';
import Daftar from './Components/Daftar/Daftar'
function App() {
  const Plastik = [
      {id:'PL1',nama:'Plastik 2Kg',harga:2000 },
      {id:'PL2',nama:'Plastik 5Kg',harga:1500 },
      {id:'PL3',nama:'Plastik 4Kg',harga:4000 },
      {id:'PL4',nama:'Plastik 3Kg',harga:1000 },
      {id:'PL5',nama:'Plastik 1Kg',harga:3000 },
  ]
  const Kresek = [
      {id:'KR1',nama:'Kresek 2Kg',harga:2000 },
      {id:'KR2',nama:'Kresek 5Kg',harga:1500 },
      {id:'KR3',nama:'Kresek 4Kg',harga:4000 },
      {id:'KR4',nama:'Kresek 3Kg',harga:1000 },
      {id:'KR5',nama:'Kresek 1Kg',harga:3000 },
      {id:'KR6',nama:'Kresek 2Kg',harga:2000 },
      {id:'KR7',nama:'Kresek 5Kg',harga:1500 },
      {id:'KR8',nama:'Kresek 4Kg',harga:4000 },
      {id:'KR9',nama:'Kresek 3Kg',harga:1000 },
      {id:'KR10',nama:'Kresek 1Kg',harga:3000 },
  ]
  const Sedotan = [
    {id:'SD1',nama:'Sedotan 5cm',harga:2000 },
    {id:'SD2',nama:'Sedotan Colorful',harga:1500 },
    {id:'SD3',nama:'Sedotan 10cm',harga:4000 },
    {id:'SD4',nama:'Sedotan Sendok',harga:1000 },
    {id:'SD5',nama:'Sedotan 20cm',harga:3000 },
    {id:'SD6',nama:'Sedotan Boba',harga:2000 },
    {id:'SD7',nama:'Sedotan Bengkok',harga:1500 },
    {id:'SD8',nama:'Sedotan Lucu',harga:4000 },
    {id:'SD9',nama:'Sedotan 1M',harga:1000 },
    {id:'SD10',nama:'Sedotan 1km',harga:3000 },
]
const Bumbu = [
    {id:'BU1',nama:'Masako Sapi',harga:2000 },
    {id:'BU2',nama:'Masako Ayam',harga:1500 },
    {id:'BU3',nama:'Royco Sapi',harga:4000 },
    {id:'BU4',nama:'Royco Ayam',harga:1000 },
    {id:'BU5',nama:'Rendang Instan',harga:3000 },
    {id:'BU6',nama:'Soto Instan',harga:2000 },
    {id:'BU7',nama:'Racik Nasi Goreng',harga:1500 },
    {id:'BU8',nama:'Sasa 200gr',harga:4000 },
    {id:'BU9',nama:'Garam',harga:1000 },
    {id:'BU10',nama:'Kecap Bango',harga:3000 },
    {id:'BU11',nama:'Kecap Inggris',harga:2000 },
    {id:'BU12',nama:'Merica Bubuk',harga:1500 },
    {id:'BU13',nama:'Lada Bubuk',harga:4000 },
    {id:'BU14',nama:'Bon Cabe',harga:1000 },
    {id:'BU15',nama:'Saos sambal',harga:3000 },
    {id:'BU16',nama:'Saos Tomat',harga:2000 },
    {id:'BU17',nama:'Saos Tiram',harga:1500 },
    {id:'BU18',nama:'Saos Saos',harga:4000 },
    {id:'BU19',nama:'Saos Soas',harga:1000 },
    {id:'BU20',nama:'Kecap',harga:3000 },
]
const Sterofoam = [
    {id:'ST1',nama:'Sterofoam Bubur',harga:2000 },
    {id:'ST2',nama:'Sterofoam 1',harga:1500 },
    {id:'ST3',nama:'Sterofoam 2',harga:4000 },
    {id:'ST4',nama:'Sterofoam 3',harga:1000 },
    {id:'ST5',nama:'Sterofoam 4',harga:3000 },
]

  const [login,setLogin] = useState(false)
  useEffect(()=>{
    let log = localStorage.getItem('loginInfo')
    if (log === '1'){
      setLogin(true)
    }
    else {
      setLogin(false)
    }
    })
  const [data,setData] = useState([...Plastik])
  const [img,setImg] = useState(plastikImage)
  const getFilter = (props) => {
      if (props.toLowerCase() ==='plastik'){
        setData(Plastik)
        setImg(plastikImage)
      }
      else if (props.toLowerCase() === 'kresek'){
        setData(Kresek)
        setImg(kresekImage)
      }
      else if (props.toLowerCase() === 'sedotan'){
        setData(Sedotan)
        setImg(sedotanImage)
      }

      else if (props.toLowerCase() === 'bumbu'){
        setData(Bumbu)
        setImg(bumbuImage)
      }
      else if (props.toLowerCase() === 'sterofoam'){
        setData(Sterofoam)
        setImg(sterofoamImage)
      }
  }

  const [belanjaan,setBelanjaan] = useState([])
  const [jumlahBarang,setJumlahBarang] = useState([]);
  const [cartDisplay,setCartDisplay] = useState(0)
  const [orderDisplay,setOrderDisplay] = useState(0)
  const [totalHarga,setTotalHarga] = useState(0)
  const cartHandler = (display) =>{
    setCartDisplay(display)
  }
  const orderHandler = () =>{
    setCartDisplay(0)
    setOrderDisplay(1)
  }
  const backOrderHandler = () =>{
    setCartDisplay(1)
    setOrderDisplay(0)
  }
  const closeOrderHandler = () =>{
    setCartDisplay(0)
    setOrderDisplay(0)
    setTotalHarga(0)
    setJumlahBarang([])
    setBelanjaan([])
    swal("Order Selesai", "Pesanan Anda Telah Terkirim!", "success");
  }
  const barangHandler = (data) =>{
    localStorage.setItem('loginInfo','1')
    let idBarang = data.substring(0,2)
    if (idBarang === 'PL'){
      const indeks = Plastik.findIndex(x => x.id === data)
      const indeksCart = belanjaan.findIndex(x=>x.id === data)
      if(belanjaan.some(el => el.id === data)){
        const newJumlahBarang = [...jumlahBarang]
        newJumlahBarang[indeksCart] = Number(newJumlahBarang[indeksCart])+1
        setJumlahBarang(newJumlahBarang)
        setTotalHarga(totalHarga+Plastik[indeks].harga)
      }
      else{
        setBelanjaan((prevCart)=>{
        const newCart = {...Plastik[indeks],jumlah:1}
        return[...prevCart,newCart]
      })
        setJumlahBarang((prevBarang=>{
          return[...prevBarang,1]
        }))
        setTotalHarga(totalHarga+Plastik[indeks].harga)
    }
    }
    else if (idBarang === 'KR'){
      const indeks = Kresek.findIndex(x => x.id === data)
      const indeksCart = belanjaan.findIndex(x=>x.id === data)
      if(belanjaan.some(el => el.id === data)){
        const newJumlahBarang = [...jumlahBarang]
        newJumlahBarang[indeksCart] = Number(newJumlahBarang[indeksCart])+1
        setJumlahBarang(newJumlahBarang)
        setTotalHarga(totalHarga+Kresek[indeks].harga)
      }
      else{
        setBelanjaan((prevCart)=>{
        const newCart = {...Kresek[indeks],jumlah:1}
        return[...prevCart,newCart]
      })
        setJumlahBarang((prevBarang=>{
          return[...prevBarang,1]
        }))
        setTotalHarga(totalHarga+Kresek[indeks].harga)
    }
    }
    else if (idBarang === 'SD'){
      const indeks = Sedotan.findIndex(x => x.id === data)
      const indeksCart = belanjaan.findIndex(x=>x.id === data)
      if(belanjaan.some(el => el.id === data)){
        const newJumlahBarang = [...jumlahBarang]
        newJumlahBarang[indeksCart] = Number(newJumlahBarang[indeksCart])+1
        setJumlahBarang(newJumlahBarang)
        setTotalHarga(totalHarga+Sedotan[indeks].harga)
      }
      else{
        setBelanjaan((prevCart)=>{
        const newCart = {...Sedotan[indeks],jumlah:1}
        return[...prevCart,newCart]
      })
        setJumlahBarang((prevBarang=>{
          return[...prevBarang,1]
        }))
        setTotalHarga(totalHarga+Sedotan[indeks].harga)
    }
    }
    else if (idBarang === 'BU'){
      const indeks = Bumbu.findIndex(x => x.id === data)
      const indeksCart = belanjaan.findIndex(x=>x.id === data)
      if(belanjaan.some(el => el.id === data)){
        const newJumlahBarang = [...jumlahBarang]
        newJumlahBarang[indeksCart] = Number(newJumlahBarang[indeksCart])+1
        setJumlahBarang(newJumlahBarang)
        setTotalHarga(totalHarga+Bumbu[indeks].harga)
      }
      else{
        setBelanjaan((prevCart)=>{
        const newCart = {...Bumbu[indeks],jumlah:1}
        return[...prevCart,newCart]
      })
        setJumlahBarang((prevBarang=>{
          return[...prevBarang,1]
        }))
        setTotalHarga(totalHarga+Bumbu[indeks].harga)
    }
    }
    if (idBarang === 'ST'){
      const indeks = Sterofoam.findIndex(x => x.id === data)
      const indeksCart = belanjaan.findIndex(x=>x.id === data)
      if(belanjaan.some(el => el.id === data)){
        const newJumlahBarang = [...jumlahBarang]
        newJumlahBarang[indeksCart] = Number(newJumlahBarang[indeksCart])+1
        setJumlahBarang(newJumlahBarang)
        setTotalHarga(totalHarga+Sterofoam[indeks].harga)
      }
      else{
        setBelanjaan((prevCart)=>{
        const newCart = {...Sterofoam[indeks],jumlah:1}
        return[...prevCart,newCart]
      })
        setJumlahBarang((prevBarang=>{
          return[...prevBarang,1]
        }))
        setTotalHarga(totalHarga+Sterofoam[indeks].harga)
      }
    }
  }

  const [loginDisplay,setLoginDisplay] = useState(0)
  const loginHandler = (display) =>{
    setLoginDisplay(1)
  }

  const [daftarDisplay,setDaftarDisplay] =useState(0)
  const daftarHandler = (display) =>{
    setDaftarDisplay(1)
    setLoginDisplay(0)
  }
  const closeDaftarHandler = (display) =>{
    setDaftarDisplay(0)
  }
  const loginTutupHandler = (display) =>{
    setLoginDisplay(display)
  }
  async function submitNewUsername(uname) {
    const respose = await fetch('https://larisplastik-b5ebf-default-rtdb.asia-southeast1.firebasedatabase.app/UserName.json',{
      method:'POST',
      body: JSON.stringify(uname),
      headers:{
        'Content-Type':'application/json'
      }
    })
    const data = await respose.json()
    console.log(data)
  }
  return(
    <React.Fragment>
      {daftarDisplay===1&& <Daftar submitNewUsername={submitNewUsername} closeDaftarHandler = {closeDaftarHandler}/>}
      {cartDisplay===1 && <Cart orderHandler={orderHandler} tutupHandler={cartHandler} belanjaan={belanjaan} jumlahBarang={jumlahBarang} totalHarga={totalHarga}/>}
      {loginDisplay===1 && <LoginForm loginTutupHandler={loginTutupHandler} daftarHandler = {daftarHandler}/>}
      {orderDisplay===1 && <Order jumlahBarang={jumlahBarang} belanjaan={belanjaan} backOrderHandler={backOrderHandler} totalHarga={totalHarga} closeOrderHandler={closeOrderHandler}/>}
      <Header belanjaan={belanjaan} cartHandler={cartHandler} loginHandler = {loginHandler}/>
      <Body getFilter={getFilter} data={data} img={img} barangHandler={barangHandler}/>
      {login===true && <p>test Login</p>}
      {login===false&& <p>Testing unLogin</p>}
    </React.Fragment>
  )  
}

export default App;
