import Modal from "../UI/Modal"
import classes from './Daftar.module.css'
const Daftar = (props) =>{
    const closeDaftarHandler = () => {
        props.closeDaftarHandler(1)
    }

    const daftarSubmitHandler = (e) =>{
        e.preventDefault()
        const userName = document.getElementById('daftarUname')
        props.submitNewUsername(userName.value)
    }
    return (
        <Modal>
            <form onSubmit={daftarSubmitHandler} className={classes.actions}>
                <label>Username</label>
                <input type='text' id='daftarUname' placeholder='Masukkan UserName Anda'/>
                <br/>
                <button onClick={closeDaftarHandler} className={classes['button--alt']}>Tutup</button>
                <button type='submit' className={classes.button}>Daftar</button>
                <p>Sudah memiliki akun? <button className={classes.btnDaftar}>Klik Disini</button></p>
            </form>

        </Modal>
    )
}

export default Daftar