import classes from './KatalogForm.module.css'

const KatalogForm = props =>{
    const barangHandler = (e) => {
        e.preventDefault()
        props.barangHandler(props.id)
    }
    return <form className={classes.form}>
        <button onClick={barangHandler}>
            + Add
        </button>
    </form>
}

export default KatalogForm