import React from 'react';
import HeaderCartButton from '../Cart/HeaderCartButton';
import classes from './Header.module.css'
import headerImage from '../../Assets/Laris Plastik.jpg'
import LoginButton from '../Login/LoginButton';

const Header = (props) =>{
    const cartHandler = (display) =>{
        props.cartHandler(display)
    }
    const loginHandler = (display) =>{
        props.loginHandler(display)
    }
    return (
    <React.Fragment>
        <header className={classes.header}>
            <h2>Laris Plastik</h2>
            <div className={classes.buttonHeader}>
                <LoginButton loginHandler={loginHandler}/>
                <HeaderCartButton belanjaan={props.belanjaan} cartHandler={cartHandler}/>
            </div>
        </header>
        <div className={classes['main-image']}>
            <img src={headerImage}/>
        </div>
    </React.Fragment> 
    )
}

export default Header