import React from 'react'
import { Dropdown } from 'semantic-ui-react'

const countryOptions = [
    { key: 'Plastik', value: 'pl', text: 'Plastik' },
    { key: 'Kresek', value: 'kr', text: 'Kresek' },
    { key: 'Bumbu', value: 'bu', text: 'Bumbu' },
    { key: 'Sedotan', value: 'se', text: 'Sedotan' },
    { key: 'Sterofoam', value: 'st', text: 'Sterofoam' },
]


const DropdownExampleSearchSelection = (props) => {
    const filterBarangHandler = (e) =>{
        const valuText = e.target.value
        if (valuText == null){
            props.getFilter(e.target.innerText)
        }
        else{
            props.getFilter(e.target.value)
        }
        
    }
    return(
        <Dropdown onChange={filterBarangHandler}
        placeholder='Pilih Barang'
        fluid
        search
        selection
        options={countryOptions}
        />
    )
}

export default DropdownExampleSearchSelection